*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	font-size: 16px;
}

body {
	box-sizing: border-box;
	color: $main-color;
	font-weight: 400;
	font-family: $main-font-family;
	background-color: $color-green;
}

img {
	max-width: 100%;
	height: auto;
	vertical-align: middle;
}

video {
	vertical-align: middle;
}

figure {
	margin: 0;
	padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: $second-font-family;
}

ul {
	margin: 0;
	padding: 0;
	list-style: none;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	clip: rect(0 0 0 0);
}

.site-container {
	width: 100%;
	max-width: 1470px;
	margin: 0 auto;
	padding: 0 15px;
}

.page-wrapper {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
}

.page-main {
	flex-grow: 1;
}

.button_reset {
	margin: 0;
	padding: 0;
	background-color: transparent;
	border: none;
}

.main-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	width: 300px;
	margin-top: 60px;
	padding: 16px 35px;
	color: $color-white;
	font-weight: 800;
	font-size: 2rem;
	font-family: $main-font-family;
	border: 2px solid $color-white;
	border-radius: 3px;
	cursor: pointer;
	transition: color 0.3s, background-color 0.3s, opacity 0.3s ease-in-out, visibility 0.3s ease-in-out, transform 0.3s ease-in-out;

	&:hover {
		color: $color-green;
		background-color: $color-white;
	}
}
